/* -------------------------------------------------------------------------------------------------
  # Icons
------------------------------------------------------------------------------------------------- */
.icon {
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  font-style: normal;
  color: $cm_color-black;
  text-align: left;
  text-indent: -9999px;
  direction: ltr;

  &::before,
  &::after {
    content: "";
    pointer-events: none;
  }

  &-caret {
    width: 20%;
    height: 20px;
    margin: 3px 10px;

    &::before,
    &::after {
      width: 7px;
      height: 1px;
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto 0;
      right: 2px;
      box-shadow: inset 0 0 0 32px;
      transform-origin: right;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }

  &-close {
    width: 30px;
    height: 30px;
    margin: 2px;
    transform: rotate(45deg);

    &::before,
    &::after {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      box-shadow: inset 0 0 0 32px;
    }

    &::before {
      width: 20px;
      height: 2px;
    }

    &::after {
      height: 20px;
      width: 2px;
    }
  }
}
