/* -------------------------------------------------------------------------------------------------
  # Menu(s)
------------------------------------------------------------------------------------------------- */
.cm-panel {
  > ul {
    margin: 0 auto;
    padding: 0;
    position: relative;
    width: 90%;
    display: block;
    list-style: none;

    li {
      position: relative;

      > ul {
        display: none;
        margin: 0;
        padding: 0;
        position: relative;

        a {
          position: relative;
          font-size: 1rem;
          padding: 0.5em 2em;
        }
      }
    }

    a {
      position: relative;
      display: block;
      padding: 0.25em 0;
      overflow: hidden;
      text-decoration: none;
    }
  }
}
